import React, { useState } from 'react';
import './Form.scss';
import { useAppDispatch, useAppSelector } from '../../hooks/redux';
import { TextField } from '@mui/material';
import { storeSlice } from '../../store/reducers/StoreSlice';

export const Form = () => {
  const [userName, setUserName] = useState<string>('');
  const [birthDate, setStartDate] = useState<string>('');
  const [yearsOld, setYearsOld] = useState<string>('');

  const currentDate = new Date();

  const dispatch = useAppDispatch();
  const { userData } = useAppSelector((state) => state.storeReducer);

  return (
    <div className="form">
      <label htmlFor="name">{`': `}</label>
      <TextField
        id="name"
        name="name"
        onChange={(e) => setUserName(e.target.value)}
        size="small"
        value={userName}
      />
      <label className="label" htmlFor="date">
         :{' '}
      </label>
      <TextField
        id="date"
        name="date"
        size="small"
        type="date"
        onChange={(e) => {
          setStartDate(e.target.value.toString());
          setYearsOld(
            ((currentDate.getTime() - new Date(e.target.value).getTime()) / 31536000000)
              .toString()
              .slice(0, 2)
          );
        }}
      />
      <button
        className="btn"
        id="form_btn"
        onClick={() => {
          dispatch(storeSlice.actions.setUserData([userName, birthDate, yearsOld]));
        }}
      >
        
      </button>
    </div>
  );
};
